html, body {
  margin: 0;
  padding: 0;
}

canvas {
  display: block;
}

input, select, textarea, button {
  font-family: inherit;
  font-size: inherit;
}

button {
  border: 1px solid black;
  border-bottom: none;
}

/* HIDE RADIO */
[type=radio] { 
  position: absolute;
  opacity: 0;
  width: 0;
  height: 0;
}

/* IMAGE STYLES */
[type=radio] + * {
  cursor: pointer;
}

[type=radio] + *:hover {
  background-color: lightgray;
}

/* CHECKED STYLES */
[type=radio]:checked + * {
  background: lightblue;
}
